.model {
	margin-top: 140px;
	margin-bottom: 200px;

	@media(max-width:1280px) {
		margin-top: 50px;
		margin-bottom: 50px;
	}

	&__cards {
		display: flex;
		flex-wrap: wrap;
		justify-content: space-between;
		gap: 30px;

		@media(max-width:1280px) {
			justify-content: center;
		}
	}

	.card {
		display: flex;
		flex-direction: column;
		justify-content: flex-start;
		align-items: center;
		text-align: center;
		transition: all .3s ease-in-out;

		&:hover {
			.card__btn {
				opacity: 1;
				visibility: visible;
				transition: all .3s ease-in-out;
			}
		}

		&__btn {
			visibility: hidden;
			opacity: 0;
			transition: all .3s ease-in-out;
		}

		&__img {
			max-width: 403px;
			height: 295px;
			width: 100%;
			transition: all .3s ease-in-out;

			img {
				width: 100%;
				height: 100%;
				object-fit: contain;
			}
		}

		&:hover .card__img {
			transform: scale(1.2);
		}

		&__title {
			font-size: 36px;
			font-weight: 400;
			margin-bottom: 15px;
		}

		&__text {
			div {
				font-size: 15px;
				font-weight: 300;

				&:last-child {
					margin-top: 5px;
				}
			}
		}

	}

	&__heading {
		margin-top: 140px;

		@media(max-width:1280px) {
			margin-top: 50px;
		}

	}

	&__text {
		p {
			font-size: 32px;
			font-weight: 300;

			@media(max-width:1280px) {
				font-size: 28px;
			}

			@media(max-width:1150px) {
				font-size: 24px;
			}

			span {
				color: #565A64;
				font-size: 32px;
				font-weight: 300;

				@media(max-width:1280px) {
					font-size: 28px;
				}

				@media(max-width:1150px) {
					font-size: 24px;
				}

			}

			&:first-child {
				display: flex;
				justify-content: flex-end;
				margin-bottom: 15px;
				margin-left: 50px;
				text-align: right;
			}
		}
	}
}