@use '../abstracts/variables' as var;

.map {
	margin-top: 250px;
	margin-bottom: 140px;

	@media(max-width:1480px) {
		margin-top: 100px;
		margin-bottom: 50px;
	}

	@media(max-width:992px) {
		margin-top: 50px;
		margin-bottom: 50px;
	}

	&__right {
		width: 70%;
		max-height: 1000px;

		@media(max-width:1024px) {
			width: 100%;
		}

		img {
			height: 100%;
			width: 100%;
			object-fit: cover;
		}

		iframe {
			width: 100%;
			height: 100%;
		}
	}

	.left {
		@media(max-width:1024px) {
			width: 100%;
		}

		width:30%;
		padding:70px 65px 60px 55px;
		background-color: white;
		max-height: 1000px;
		overflow-y: auto;

		@media(max-width:1480px) {
			padding: 50px;
		}

		@media(max-width:1280px) {
			padding: 25px;
		}

		&::-webkit-scrollbar {
			width: 15px;
			height: 124px;
			padding: 0 10px;
		}

		&::-webkit-scrollbar-track {
			background: #D4D4D4;
		}


		&::-webkit-scrollbar-thumb {
			background: white;
			border-right: 6px solid #D4D4D4;
			border-left: 6px solid #D4D4D4;


		}

		&__title {
			display: flex;
			font-size: 36px;
			align-items: center;
			margin-bottom: 40px;

			@media(max-width:1480px) {
				font-size: 30px;
			}

			@media(max-width:1280px) {
				font-size: 24px;
			}

			svg {
				path {
					stroke: var.$dark;
				}
			}
		}

		.item {
			border-bottom: 1px #E2E2E2 solid;

			&:not(:last-child) {
				margin-bottom: 40px;
			}

			&__title {
				font-size: 36px;
				font-weight: 300;
				margin-bottom: 15px;

				@media(max-width:1480px) {
					font-size: 30px;
				}

				@media(max-width:1280px) {
					font-size: 24px;
				}
			}

			&__adress {
				margin-bottom: 15px;
			}

			&__schedule {
				margin-bottom: 15px;
			}

			&__info {
				div {
					display: flex;
					align-items: center;
					gap: 10px;
					font-weight: 300;
					margin-bottom: 15px;

					&:last-child {
						margin-bottom: 28px;
					}
				}
			}

		}
	}

	&__wrapper {
		display: flex;

		@media(max-width:1024px) {
			flex-direction: column-reverse;
		}
	}
}